import React from "react";
import classes from "./CompaniesList.module.scss";

const CompaniesList = props => {
	// Метод для вывода компаний на страницу
	const renderCompanies = (companies) => {
		const companyArr = [];

		for (let name in companies) {

			if (props.currency !== companies[name].currency) continue;

			const key = `${props.afterTax} + ${Math.random()}`;

			const arrItem = (
				<div className={classes.row} key={key}>
					<span datatype="name" className={classes.CompanyName}
						  onClick={props.showModalHandlerDelete}>{name}</span>
					<span datatype="count">{companies[name].count}</span>
					<span datatype="dividendPerShare">{companies[name].dividendPerShare}</span>
					<span>{Math.ceil(companies[name].count * companies[name].dividendPerShare * 100) / 100}</span>
				</div>
			);

			companyArr.push(arrItem);
		}

		return companyArr
	};
	// Рендер футера с налогами
	function renderFooter(companies) {
		const companyArr = [];

		let beforeTax = 0;
		let afterTax = 0;

		for (let name in companies) {
			if (props.currency !== companies[name].currency) continue;

			const bt = companies[name].count * companies[name].dividendPerShare;
			const at = bt - (bt * 0.13);

			beforeTax +=bt;
			afterTax +=at;
		}

		const arrItem = (
			<div key={afterTax} className={classes.footer}>
				<div className={classes.Total}>
						<span>
							Прибыль до вычета налогов
						</span>
					<span>
							{Math.ceil((beforeTax) * 100) / 100}
						</span>
				</div>

				<div className={classes.Total}>
						<span>
						Прибыль после вычета налогов
						</span>

					<span className={classes.afterTax}>
							{Math.ceil((afterTax) * 100) / 100}
						</span>
				</div>
			</div>
		);

		companyArr.push(arrItem);
		return companyArr
	}

	return (
		<div className={classes.CompaniesList}>
			<div className={classes.header}>
				<span>Имя компании</span>
				<span>Акции </span>
				<span>Дивиденд</span>
				<span>Прибыль (год)</span>
			</div>
			{
				renderCompanies(props.companies)
			}
			{
				renderFooter(props.companies)
			}
		</div>
	)
};

export default CompaniesList;
