import is from "is_js";

export function createAddFormInputs(config, validation) {
	return {
		...config,
		validation,
		valid: !validation,
		touched: false,
		value: ""
	}
}

export function createUpdFormInputs(config, validation) {
	return {
		...config,
		validation,
		valid: true,
		touched: true,
		value: ""
	}
}

// Валидация параметров из validation
export function validateControl(value, validation) {
	if (!validation) return true;

	let isValid = true;

	if (validation.required) {
		isValid = value.trim() !== '' && isValid
	}
	if (validation.type) {
		isValid = is.number(+value) && isValid
	}

	return isValid
}