import React from "react";
import classes from './Main.module.scss'
import CompaniesList from "../../components/CompaniesList/CompaniesList";
import {Title} from "../../components/Title/Title";
import {Button} from "../../components/UI/Button/Button";
import {Modal} from "../../hoc/Modal/Modal";
import {Input} from "../../components/UI/Input/Input";
import {FadeBg} from "../../hoc/Modal/FadeBg";
import axios from '../../axios/axios-settings'
import {Preloader} from "../../components/Preloader/Preloader";
import {createAddFormInputs, createUpdFormInputs, validateControl} from "../../Modules/formValidation";

// Функции для генерации полей для инпутов
function createOptionForAddInputs(name, errMessage, obj) {
	return createAddFormInputs({
			label: name,
			type: "text",
			errorMessage: errMessage,
			className: [""],
		}, obj
	)
}

function createOptionForUpdInputs(name, errMessage, obj) {
	return createUpdFormInputs({
			label: name,
			type: "text",
			errorMessage: errMessage,
			className: [""],
		}, obj
	)
}

// TODO: создать общую переменную this.state.key, которая будет определять пользователя
export default class Main extends React.Component {
	state = {
		key: "aHmcqjPPBwXvW397FPL31TtkwfJ3",
		nameForDelete: "",
		showPreloader: true,
		isFormValid: false,
		modals: {
			isOpenModalAddCompany: false,
			isOpenModalUpdateCompany: false
		},
		formAddInputs: {
			name: createOptionForAddInputs("Имя компании", "Поле не может быть пустым", {required: true}),
			count: createOptionForAddInputs("Количество акций", "Введите число", {required: true, type: "number"}),
			dividendPerShare: createOptionForAddInputs("Размер дивидендов (в год)", "Введите число", {
				required: true,
				type: "number"
			})
		},
		formUpdInputs: {
			count: createOptionForUpdInputs("Количество акций", "Введите число", {required: true, type: "number"}),
			dividendPerShare: createOptionForUpdInputs("Размер дивидендов (в год)", "Введите число", {
				required: true,
				type: "number"
			})
		},
		company: {
			name: "",
			count: "",
			dividendPerShare: "",
			currency: ""
		},
		companies: {},
		currency: [],
	};

	showModalHandler = (e, popup) => {
		const modals = {...this.state.modals};
		modals[popup] = true;

		if (popup === "isOpenModalUpdateCompany") {
			const nameForDelete = e.target.parentNode.querySelector('span').textContent;
			this.setState({modals, nameForDelete});
			return
		}

		this.setState({modals});
	};
	closeModalHandler = (e, popup) => {
		const modals = {...this.state.modals};
		const formAddInputs = {...this.state.formAddInputs};

		modals[popup] = false;

		let isFormValid = false;

		Object.keys(formAddInputs).forEach(name => {
			const control = {...formAddInputs[name]};
			control.valid = false;
			control.touched = false;
			formAddInputs[name] = control;
		});

		this.setState({
			modals,
			formAddInputs,
			isFormValid
		})
	};

	// Создание компании для того, чтобы отправить её в будущем в базу
	createCompany = (name, count, dividendPerShare, currency) => {
		return {
			[name]: {
				count,
				dividendPerShare,
				currency
			}
		}
	};
	// Обновление данных по компаниям
	updateCompany = async () => {
		const {name, count, dividendPerShare} = this.state.company;

		const response = await axios.put(`/${this.state.key}/portfolio/companies/${name}/count.json`, count);
		await axios.put(`/${this.state.key}/portfolio/companies/${name}/dividendPerShare.json`, dividendPerShare);

		if (response.status === 200) {
			this.setState({showPreloader: true});

			this.renderCompanies();
			this.closeModalHandler(null, "isOpenModalUpdateCompany")
		}
	};
	// Удаление компаний
	deletePositionHandler = async (name) => {
		const response = await axios.delete(`/${this.state.key}/portfolio/companies/${name}.json`);

		if (response.status === 200) {
			this.setState({showPreloader: true});

			this.renderCompanies();
			this.closeModalHandler(null, "isOpenModalUpdateCompany")
		}
	};
	// Собираю данные для редактирования/удаления компании (клик на имя компании)
	preUpdateCompany = (e) => {
		const company = Object.assign({}, this.state.company);
		const parent = e.target.parentNode;

		company.name = e.target.textContent;
		company.count = parent.querySelector("span[datatype='count']").textContent;
		company.dividendPerShare = parent.querySelector("span[datatype='dividendPerShare']").textContent;

		this.setState({company, isFormValid: true});

		this.showModalHandler(e, "isOpenModalUpdateCompany");
	};
	// Отправка запроса на сервер и добавление компании в общий список
	mainModalFormAddCompany = async (e) => {
		e.preventDefault();
		try {
			const {name, count, dividendPerShare, currency} = this.state.company;
			const company = this.createCompany(name, count, dividendPerShare, currency);
			const response = await axios.patch(`/${this.state.key}/portfolio/companies.json`, company);

			if (response.status === 200) {
				const modals = {...this.state.modals};
				modals.isOpenModalAddCompany = false;

				this.setState({
					company,
					modals
				});

				this.renderCompanies();
			}
		} catch (e) {
			console.error(e)
		}
	};

	// Получаю значения из инпутов (onChange) и записываю их в компанию, чтобы потом отправлять её на сервер
	onChangeHandler(e, inputName, inputsObj) {
		const company = {...this.state.company};
		const Inputs = {...this.state[inputsObj]};
		// control - это name, count, dividensPerShare
		const control = {...Inputs[inputName]};

		// Валидация
		control.touched = true;
		control.value = e.target.value;
		control.valid = validateControl(control.value, control.validation);

		Inputs[inputName] = control;
		company[inputName] = e.target.value;

		let isFormValid = true;

		// Валидация формы (активные кнопки или нет)
		Object.keys(Inputs).forEach(name => {
			isFormValid = Inputs[name].valid && isFormValid;
		});

		this.setState({
			company,
			[inputsObj]: Inputs,
			isFormValid
		});
	}

	// Select onChange
	selectHandler = (e) => {
		const company = {...this.state.company};
		company.currency = e.target.value;

		this.setState({company})
	};
	// Асинхронная операция на сервер, рендерит список компаний
	renderCompanies = async () => {
		try {
			// Импорт портфеля пользователя
			const response = await axios.get(`/${this.state.key}.json`);
			// Импорт компаний
			const {companies} = response.data.portfolio;
			// Количество валют
			const currency = [...this.state.currency];
			const company = {...this.state.company}; // Импортирую ради того, чтобы задать валюту по умолчанию USD
			company.currency = "USD";

			if (response.status === 200) {
				this.setState({companies, company, showPreloader: false});
				// Ищу сколько всего валют
				Object.keys(this.state.companies).forEach(companyName => {
					const crc = this.state.companies[companyName].currency;
					if (!currency.includes(crc)) currency.push(crc)
				});

				this.setState({currency})
			}
		} catch (e) {
			console.error(e)
		}
	};

	renderInputs(name, defVal) {
		return Object.keys(this.state[name]).map((inputName, index) => {
			const inputObj = this.state[name][inputName];

			return (
				<Input
					key={inputName + index}
					type={inputObj.type}
					value={inputObj.value}
					classes={inputObj.className}
					htmlFor={inputName + index}
					valid={inputObj.valid}
					touched={inputObj.touched}
					label={inputObj.label}
					defaultValue={defVal ? this.state.company[inputName] : null}
					errorMessage={inputObj.errorMessage}
					onChange={e => this.onChangeHandler(e, inputName, name)}
				/>
			)
		})
	};

	componentDidMount() {
		this.renderCompanies();
	}

	render() {
		return (
			<div className={classes.Main}>
				<Title title="Список компаний"/>
				{
					this.state.showPreloader
						? <Preloader/>
						:
						this.state.currency.map((currency, index) => {
							return (
								<div className={classes.tableWrapper} key={index}>
									<h3>{currency}</h3>
									<CompaniesList
										currency={currency}
										companies={this.state.companies}
										showModalHandlerDelete={e => this.preUpdateCompany(e)}
									/>
								</div>
							)
						})


				}
				<div className={classes.addSection}>
					<Button
						classses={["green"]}
						name="Добавить компанию"
						onClick={e => this.showModalHandler(e, "isOpenModalAddCompany")}
						disabled={true}
					/>
				</div>
				{/* Модальное окно для добавления компании */}
				{
					this.state.modals.isOpenModalAddCompany
						?
						<React.Fragment>
							<Modal>
								<h3 className={classes.MainModalTitle}>Добавить компанию</h3>

								<form onSubmit={this.mainModalFormAddCompany}>
									{
										this.renderInputs("formAddInputs")
									}
									<select onChange={this.selectHandler}>
										<option value="USD">USD</option>
										<option value="RUB">RUB</option>
									</select>
									<Button
										disabled={this.state.isFormValid}
										classses={["blue"]}
										name="Добавить компанию"
										onClick={e => this.showModalHandler(e, "isOpenModalAddCompany")}
									/>
								</form>
							</Modal>
							<FadeBg onClick={e => this.closeModalHandler(e, "isOpenModalAddCompany")}/>
						</React.Fragment>
						: null
				}
				{/* Модальное окно для удаления/редактирования компании */}
				{
					this.state.modals.isOpenModalUpdateCompany
						?
						<React.Fragment>
							<Modal>
								<h3 className={classes.deleteModal}>
									Карточка компании "{this.state.nameForDelete}"
								</h3>
								{
									this.renderInputs("formUpdInputs", true)
								}
								<div className={classes.modalDeleteBtnBox}>

									<Button
										name="Удалить"
										classses={["red"]}
										onClick={e => this.deletePositionHandler(this.state.nameForDelete)}
										disabled={this.state.isFormValid}
									/>

									<Button
										name="Изменить"
										classses={["green"]}
										onClick={this.updateCompany}
										disabled={this.state.isFormValid}
									/>
								</div>
							</Modal>
							<FadeBg onClick={e => this.closeModalHandler(e, "isOpenModalUpdateCompany")}/>
						</React.Fragment>
						: null
				}
			</div>
		)
	}
}